	import moment from 'moment'
	import util from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import monthPicker from 'c%/MonthPicker.vue'
	
	export default{
		mixins: [common,globals],
		components: {
			monthPicker
		},
		data () {
			return {			
				data: [],
				uri: 'employee_lesson_hours?eid=',
				currentTime: '',
			}
		},
		mounted () {
			let time = moment(new Date()).format('YYYYMM')
			this.onChange(time)
		},
		methods: {
			getMonthHours (day) {
				let month = String(day).substring(0,6),
					result = 0
					this.data.forEach(d=>{
						if(String(d.int_day).indexOf(month)>-1){
							result = parseFloat(result) + parseFloat(d.lesson_hours)
						}
					})
					
				return parseFloat(result).toFixed(2)
			},
			getMonthOutput (day) {
				let month = String(day).substring(0,6),
					result = 0
					
					this.data.forEach(d=>{
						if(String(d.int_day).indexOf(month)>-1){
							result += Number(d.total_lesson_amount)
						}
					})
				
				return parseFloat(result).toFixed(2)
			},
			hasTitle (index) {
				if(index > 0){
					return String(this.data[index].int_day).substring(0,6) != String(this.data[index-1].int_day).substring(0,6)
				}
				return index==0
			},	
			getMonth (monthIndex){
				let result = this.$filter('int_date')(this.data[monthIndex].int_day)
				
				return moment(result).format('MM月')
			},
			formatDate (time) {
				time = this.$filter('int_date')(time)
				return moment(time).format('MM/DD')
			},
			showMonth (time) {
				time = this.$filter('int_date')(time)
				time = moment(time).format('YYYYMMDD')
				
				this.$refs['month_picker'].show(time)
			},
			onChange (time) {	
				this.currentTime = time
				let month = time.substring(0,4)+'-'+this.formatYY(time.substring(4,6))+'-'
				
				let start_day = month + '01'
				let end_day = month + moment(time,'YYYYMM').daysInMonth('day')
				
				this.data_uri = util.sprintf('employee_lesson_hours?int_day=[between,%s,%s]',start_day,end_day)
				this.pageIndex = 0
				if(this.mescroll){					
					this.mescroll.resetUpScroll()
				}
			},	
			formatYY (i){
				i = parseInt(i)
				if(i<10){
					return '0'+i
				}
				return i
			},
			deal_data (response) {
                let list = response.body.data.list,	
	            	total = response.body.data.total,
	            	curLength = 0
	            
	            if(this.data_uri.indexOf('int_day')>-1&&list.length==0){
                	let currentTime = this.currentTime
                	let title = util.sprintf('%s年%s月',
								currentTime.substring(0,4),
								currentTime.substring(4,6))
                	this.$vux.alert.show({
					  	title: '提示',
					  	content: title+'没有产出记录~'					  
					})
                	
                	this.mescroll.endBySize(1, 1)
                	return 
                }		            	
	            
	        	if(this.pageIndex===1){
	        		this.data = []
	        	}        	
	    		this.data = this.data.concat(list)
	    		
	    		this.mescroll.endBySize(list.length, total)	  
			}
		},
		computed: {
			data_uri:{
				get: function () {
			        return this.uri+this.eid$
		      	},
		      	set: function (v) {
			        this.uri = v + '&eid='
		      	}	  	
			}			
		}
	}
	



